<template>
  <div>
    <main> <!-- Début du main -->
      <div class="title"> <!-- title de présentation, dans le main -->
        <h1>
          <BarLoader style="margin: 0 auto; border-radius:5px" v-if="person.length == 0"></BarLoader>
          <StateDisplay :state="person.state"/>
          {{ person.name}}
          <span v-if="person.date" class="date">({{ person.date}})</span>
        </h1>
      </div>
      <StateWarn :state="person.state" />
      <div class="card content options">
          <p>Choisissez à quoi doit ressembler votre fiche de révision en cliquant sur ce qui vous convient le mieux. Vous pouvez aussi éditer la fiche avant de l'imprimer.</p>
          <h3>Taille de la fiche</h3>
          <input type="radio" id="tiny" value="tiny" v-model="size">
          <label for="tiny">Petite</label>
          <input type="radio" id="normal" value="normal" v-model="size">
          <label for="normal">A4</label>
          <h3>Conserver</h3>
          <input id="keepArtworks" type="checkbox" v-model="keepArtworks">
          <label for="keepArtworks" >Œuvres</label>
          <input id="keepNotes" type="checkbox" v-model="keepNotes">
          <label for="keepNotes">Notes</label>
          <input id="keepCode" type="checkbox" v-model="keepCode">
          <label for="keepCode"><code>Codes</code></label>
          <h3>Nom du fichier</h3>
          <input type="radio" id="kebabName" value="kebab" v-model="filename">
          <label for="kebabName">KebabCase</label>
          <input type="radio" id="normalName" value="normal" v-model="filename">
          <label for="normalName">Normal</label>
          <br><code>{{getFilename}}</code>
          <div class="action"> 
            <button @click="$router.push($route.path.replace('/print',''))" class="outline">Retour</button>
            <button @click="edition=true" v-if="!edition">Éditer</button>
            <button @click="edition=false" v-if="edition">Terminé</button>
            <button @click="print()" v-else>Imprimer</button>
          </div>
      </div>

      <div :class="['card content  printable', size,  keepCode? 'keepCode':'']" v-if="!edition" >
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="person.length == 0"></BarLoader>
        <viewer :value="person.bio" v-else-if="!edition"/>
      </div>
      <div class="admin" v-else>
        <editor  v-model="person.bio"
              :options="defaultOptions"
              height="700px"
              previewStyle="vertical"
              mode="wysiwyg"/>
      </div>

      <ul :class="['card artworks printable', keepArtworks? 'keepArtworks':'']" v-if="personArtworks.length !=0 && keepArtworks">
        <h2>Œuvres</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="artworksLoading"></BarLoader>
        <li v-for="artwork in personArtworks" :key="artwork.id">
          <router-link :to="person.slug+'/'+artwork.slug">
            <span class="title" itemprop="name">
                {{artwork.name}}
            </span>
            <span class="date" v-show="artwork.date" ref="date" itemprop="date">({{artwork.date}})</span>
          </router-link>
        </li>
      </ul>

      <div :class="['card notes  printable', keepNotes? 'keepNotes':'']" v-if="person.notes && keepNotes">
        <h2>Notes</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="person.length == 0"></BarLoader>
        <editor v-if="edition" v-model="person.notes"
            :options="defaultOptions"
            height="700px"
            previewStyle="vertical"
            mode="wysiwyg"/>
        <viewer v-else :value="person.notes" />
      </div>

    </main>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import Viewer from '@toast-ui/vue-editor/src/Viewer.vue'
import { Editor } from '@toast-ui/vue-editor'
import StateWarn from '@/components/state/Warn.vue'
import StateDisplay from '@/components/state/Display.vue'


export default {
  name: 'home',
    components: {
    Editor,
    Viewer,
    StateWarn,
    StateDisplay
  },
  data () {
      return {
          person: {}, 
          artworks: {},
          mode: 'color',
          size: 'normal',
          edition: false,
          filename: 'kebab',
          keepArtworks: false,
          keepNotes: false,
          keepCode: true,
          defaultOptions: {
            language: 'fr_FR',
            useCommandShortcut: true,
            useDefaultHTMLSanitizer: true,
            usageStatistics: true,
            hideModeSwitch: true,
            toolbarItems: [
                'heading',
                'bold',
                'italic',
                'strike',
                'divider',
                'hr',
                'quote',
                'divider',
                'ul',
                'ol',
                'indent',
                'outdent',
                'divider',
                'table',
                'image',
                'link',
                'divider',
                'code',
                'codeblock'
            ]
        }
      }
  },
created () {
    window.scrollTo(0, 0)
    this.fetchPersons()   
    .then(()=>{
        this.person = {...this.getPersons.filter(person => person.slug == this.$route.params.person)[0]}
        this.fetchArtworks(this.person.slug)
    })
    
},
computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('persons',[
        'getPerson',
        'getPersons',
        'personLoading',
        ]),
        ...mapGetters('artworks', [
          'artworksLoading',
          'getArtwork',
          'getArtworks'
        ]),
        personArtworks: function() {
            return(this.getArtworks.filter(artwork => artwork.person == this.person.name))
        },
        getFilename: function() {
          if(this.person.name) {
            return (this.filename=='normal'?this.person.name:this.person.slug)
          } else {
            return "fiche"
          }
        }
    },
    methods: {
        ...mapActions('persons', [
      'setPerson',
      'fetchPersons',
        ]),
        ...mapActions('artworks', [
          'fetchArtwork',
          'fetchArtworks',
          'clearArtwork'
        ]),
      print() {
        let title = document.title
        document.title = this.getFilename
        window.print()
        document.title = title
      }
    }
}
</script>
<style scoped>
.card.options {
    display: block;
  }
  .card.options h3 {
    margin-top: 30px;
    margin-bottom: 1px;
  }
  .card.options input {
      display: none;
  }
  .card.options code {
    color: #c1788b;
  }
  .card.options label, .card.options button {
      user-select: none;
      cursor: pointer;
      display: inline-block;
      margin: 10px 7px;
      padding: 7px 10px;
      border-radius: 7px;
      background: none;
      color: var(--b5);
      transition: all .3s;
  }
  .card.options label:hover {
      color: var(--b4)
  }
  .card.options input:checked +label{
      background: var(--b8);
      color: var(--b2);
  }
  .card.card.options input[type="radio"]:checked +label {cursor: initial;}
  .card.options button {
      border: none;
      font-size: 15px;
      background: var(--b3);
      color: var(--b8);
      margin: 10px 0 0 15px;
      padding: 10px 16px;
  }
  .card.options button:hover { 
      background: var(--b2);
  }
  .card.options button.outline {
    background: none;
    color: var(--b3);
    border: 1px solid var(--b3);
  }
  
  .card.options .action {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    justify-content:flex-end;
  }
  .card.options .action:hover {
    transform: none;
  }
  main .tui-editor-contents code {
display: none;
}

main .keepCode .tui-editor-contents code {
    display: initial !important;
}
main .keepCode .tui-editor-contents > p > code {
    display: initial !important;
}
main .tui-editor-contents >p:empty {
    display: none;
}
</style>
